import React, { useEffect } from 'react';
import { useForm } from 'react-hook-form';
import { useSelector } from 'react-redux';
import { RouteComponentProps } from 'react-router';
import { Col, Row } from 'reactstrap';
import {
  currentMomentDate,
  DATE_MONTH_YEAR_PATTERN,
  decimalMaskFormatter,
  EditForm,
  EditViewProps,
  formatDate,
  getSelectedItem,
  InputGroupInline,
  InputType,
  WindowComponent,
} from 'summer';
import { hasAdministradoraMHO } from '../../../../dependencies/administradoraMHODependency';
import { CLASS_NAME, ContaPagarMHO } from '../../../../models/dtos/mho/contaPagarMHO';
import { Reducers } from '../../../../store/ducks';
import { GlobalState } from '../../../../store/ducks/global.duck';
import HistoricoPagarMHOIS from '../../../components/mho/historicoPagarMHOIS/HistoricoPagarMHOIS';

interface ContaPagarMHOProps extends EditViewProps<ContaPagarMHO> {}

const ContaPagarMHOEditView: React.FC<ContaPagarMHOProps & RouteComponentProps> = props => {
  const selectedItem = getSelectedItem(props);
  const globalReducer = useSelector<Reducers, GlobalState>(state => state.globalReducer);
  const formProps = useForm<ContaPagarMHO>({ defaultValues: selectedItem, mode: 'onBlur' });
  const { dataCadastro } = formProps.getValues();
  const dataCadastroObjetoEdit = dataCadastro ? dataCadastro : null;
  const current = currentMomentDate();

  useEffect(
    () => {
      setTimeout(() => {
        if (dataCadastroObjetoEdit === null) {
          formProps.setValue('dataCadastro', formatDate(current.toDate(), DATE_MONTH_YEAR_PATTERN));
        } else {
          formProps.setValue('dataCadastro', dataCadastroObjetoEdit);
        }
        formProps.triggerValidation('dataCadastro');
      }, 500);
    }, // eslint-disable-next-line react-hooks/exhaustive-deps
    []
  );

  return (
    <WindowComponent title="Manutenção de Contas a Pagar" dependencies={[() => hasAdministradoraMHO(globalReducer.globalParameter)]} wikiId={1109}>
      <EditForm formProps={formProps} editViewProps={props} dtoClassName={CLASS_NAME}>
        <Row>
          <Col>
            <InputGroupInline
              formProps={{ ...formProps, validation: { required: true } }}
              id="dataCadastro"
              isPrependLabel={true}
              label="Data de Cadastro"
              placeholder={formProps.getValues().dataCadastro}
              type={InputType.DATE}
              disabled={true}
            />
          </Col>
          <Col>
            <InputGroupInline
              formProps={{ ...formProps, validation: { required: true } }}
              id="dataVencimento"
              isPrependLabel={true}
              label="Data de Vencimento"
              placeholder="Informe a Data de Vencimento"
              type={InputType.DATE}
            />
          </Col>
        </Row>
        <Row>
          <Col>
            <InputGroupInline
              formProps={{ ...formProps, validation: { min: 0.01, required: true } }}
              id="valor"
              isPrependLabel={true}
              label="Valor"
              placeholder={decimalMaskFormatter(0)}
              type={InputType.NUMBER}
            />
          </Col>
          <Col>
            <InputGroupInline
              formProps={{ ...formProps }}
              id="dataPagamento"
              isPrependLabel={true}
              label="Data de Pagamento"
              placeholder="Informe a Data de Pagamento"
              type={InputType.DATE}
            />
          </Col>
        </Row>
        <Row>
          <Col>
            <HistoricoPagarMHOIS
              formProps={{ ...formProps, validation: { required: true } }}
              label="Histórico"
              placeholder="Selecione um histórico"
              id="historico"
              isPrependLabel={true}
              inputSearchProps={{ size: 'xl' }}
            />
          </Col>
        </Row>
        <Row>
          <Col>
            <InputGroupInline
              formProps={{ ...formProps, validation: { required: true } }}
              id="descricao"
              isPrependLabel={true}
              label="Descrição"
              placeholder="Informe a Descrição."
            />
          </Col>
        </Row>
      </EditForm>
    </WindowComponent>
  );
};

export default ContaPagarMHOEditView;
